.card {
  width: 260px;
  height: 240px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  border-radius: 15px;

  position: relative;
}

.price {
	width: 50px;
	height: 25px;
	color: #111315;
	text-align: center;
	line-height: 25px;
	border-radius: 5px;
	background-color: #BEE3CC;
}
